import app from '../app';
import SocketIO from 'socket.io';

const socketIOServerState = {
  quizMasters: []
}

export const createWebsocketServer = () => new SocketIO(app.httpServer);
export const isQuizmaster = (socket) => {
  for(let s of socketIOServerState.quizMasters) {
    console.log('check socket id: ' + socket.id);
    console.log('with socket id: ' + s.id);
    if(s == socket) {
      return true;
    }
  }
  return false;
}